.headerContent {
    display: -webkit-flex;
    display: flex;
}

.headerText {
    margin: 0 auto;
    z-index: 1;
    color: rgb(255, 255, 255);
    background-image: linear-gradient(to right, rgba(255,255,255,.25) 1%, rgba(255,255,255,.85) 50%, rgba(255,255,255,.25) 99%);
    height: 200px;
    width: 100%;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 9em;
    font-weight: bold;
    text-shadow: 2px 2px 2px #5f5f5f;
    -webkit-animation: colorShift 10000ms infinite;
            animation: colorShift 10000ms infinite;
}

@media(max-width: 800px) {
    .headerText {
        font-size: 5em;
    }
}

form {
    margin: 0 auto;
}

.inputContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 25px;
    z-index: 2;
}

.inputField {
    width: 320px;
    padding: 15px;
    margin: 0 auto;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 3em;
    border-radius: 15px 10px;
	outline: none;
}

.buttons {
    text-align: center;
}

.btn-hover {
    width: 320px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 75px;
    text-align:center;
    border: none;
    background-size: 300% 100%;
    border-radius: 50px;
    moz-transition: all .75s ease-in-out;
    transition: all .75s ease-in-out;
}

.btn-hover:hover {
    background-position: 100% 0;
    moz-transition: all .75s ease-in-out;
    transition: all .75s ease-in-out;
}

.btn-hover:focus {
    outline: none;
}

.btn-hover.color-button {
    background-image: linear-gradient(to right, #7060ff, rgb(184, 72, 199), #A3A1FF, rgb(252, 101, 101));
    box-shadow: 0 4px 15px 0 rgba(126, 52, 161, 0.75);
    font-family: 'Dancing Script', cursive;
    font-size: 2.2em;
    text-shadow: 2px 2px 2px #5f5f5f;
}
.poemGenerator {
}

.poemContainer {
    margin-top: 35px;
}

p {
    font-family: 'Dancing Script', cursive;
    font-size: 2em; 
    margin: 10px;
}

.leftIndent {
    margin-left: 24px;
}

.lineSpace {
    height: 24px;
}

.leftStanza {
    margin-left: 20%;
}

.rightStanza {
    margin-left: 30%;
}

.poemTitle > h3 {
    padding: 15px;
    margin: 0 auto;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 3em;
}

.poemTitle > p {
    margin: 0 auto;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 2em;
}

.poemPlaceholder {
    width: 320px;
    padding: 15px;
    margin: 0 auto;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 3em;
    border-radius: 15px 10px;
	outline: none;
}

@media(max-width: 550px) {
    .poemContainer {
        width: 95%;
    }
    p {
        font-size: 1.5em; 
    }
    .leftStanza {
        margin-left: 5%;
    }
    
    .rightStanza {
        margin-left: 10%;
    }
}
